import React,{useState,useEffect} from 'react'
import {
  
  CButton,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CDataTable,
  CRow
} from '@coreui/react'
import {  useParams, useRouteMatch } from 'react-router-dom';
import API from 'src/api';
import { history } from 'src/history';

const TopicsList = () => {
  
  let {id}  = useParams();
  const fields = ['preFix','title', 'badge','view','delete'];
  const [topicData,setTopicData] = useState([{}]);
  const [isLoading,setIsLoading] = useState(false);
  let { url } = useRouteMatch();  

  useEffect(()=>{
    getData();
  },[]);

  async function getData(){
    try {
      setIsLoading(true);    
      var response = await API.get("/topics/list?chapterId="+id);  
      setTopicData(response.data.data);
      setIsLoading(false);
    } catch (error) {
      alert("Try Again");
    }
  }

  const deleteTopics = async (id)=>{
    try {      
      setIsLoading(true);    
      var response = await API.delete("/topics/"+id);  
      if(response.status === 201)
      {
        var data = topicData.filter((a)=>{ return a._id !== id});
        setTopicData(data);
      }
      setIsLoading(false);
    } catch (error) {
      alert("api error");
    }
  }

  return (
    <>
      <div className="">
        <CRow className="p-2 ml-auto">
        </CRow>
        <CRow>
        <CCol xs="12">
          <CCard>
            <CCardHeader>
              <span className="h4">
                Topics List
              </span>
              {/* <Link to={{pathname:`/addtopics/${id}`}} className="btn btn-primary float-right">              
                Add
              </Link>
              <Link to={{pathname:`/chapter/`}} className="btn btn-outline-info float-right mx-2">
                Back
              </Link> */}
              <div onClick={()=>{history.push(url+"add")}} className="btn btn-primary float-right">              
                Add
              </div>
              <div className="btn btn-outline-info float-right mx-2" onClick={()=>{history.goBack()}}>
                Back
              </div>
            </CCardHeader>
            <CCardBody>
            {isLoading ? <div>Loading...</div> : 
            <CDataTable
              items={topicData}
              fields={fields}
              itemsPerPage={5}
              loading={isLoading}
              pagination
              scopedSlots = {{
                'view':
                  (item)=>(
                    <td>

                    {/* <Link to={{pathname:`/unit/${item._id}`}} className="btn btn-success">                       */}
                    <div className="btn btn-success" onClick={()=>history.push(url+item._id+'/unit')}>
                      View                    
                    </div>
                    {/* </Link> */}
                    </td>
                  ),
                  'delete':
                  (item)=>(
                    <td>
                      <CButton className="btn btn-danger" onClick={(e)=> {deleteTopics(item._id);}}>Delete</CButton>                 
                    </td>
                  ),

              }}
            />
}
            </CCardBody>
          </CCard>
        </CCol>
        </CRow>
        {/* <CButton className="btn btn-primary float-right" onClick={addButton}>
          Add
        </CButton> */}
      </div>
    </>
  )
}

export default TopicsList
